import newHeader from "@/components/newHeader2/n-header";
import currencyCountry from "@/components/currencyCountry/currencyCountry";
export default {
  name: "Root",
  components: {
    newHeader,
    currencyCountry
  },
  data(){
    return {
      showHeader: true
    }
  },
  created(){
    let {name} = this.$route;
    if(name == 'share_invitation2'){
      this.showHeader = false;
    }
  }
};
