const vfilter = {
  daxie(value){
    return value.toLocaleUpperCase()
  },
	numToFix(value) {
		return parseFloat(Number(value).toFixed(8));
	},
	timers(value, type) {
		let date = new Date(value * 1000);
		let Y = date.getFullYear();
		let M =
			date.getMonth() + 1 < 10
				? "0" + (date.getMonth() + 1)
				: date.getMonth() + 1;
		let D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
		let h = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
		let m =
			date.getMinutes() < 10
				? "0" + date.getMinutes()
				: date.getMinutes();
		let s =
			date.getSeconds() < 10
				? "0" + date.getSeconds()
				: date.getSeconds();
		if (type == 1) {
			return Y + "-" + M + "-" + D + " " + h + ":" + m + ":" + s;
		} else if (type == 2) {
			return Y + "-" + M + "-" + D;
		} else if (type == 3) {
			return Y + "/" + M + "/" + D;
		} else if (type == 4) {
			return D + '/' + M + '/' + Y;
		} else if (type == 5) {
			return D + '/' + M + '/' + Y + ' ' + h + ':' + m + ':' + s;
		} else if (type == 6) {
			return D + '-' + M + '-' + Y + ' ' + h + ':' + m;
		}
	}
};

export default vfilter;
